// External
import React, { useState, useEffect } from 'react';

// Material UI
import CssBaseline from '@mui/material/CssBaseline';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import CircularProgress from '@mui/material/CircularProgress';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';

// Owned
import { useStyles } from '../styles';
import { Request, Messages } from '../../../../shared';

import { useTranslation } from 'react-i18next';
import HorizontalLogo from '../../../Shared/Components/HorizontalLogo/HorizontalLogo';
import SubscribeButton from '../../../Subscribe/Components/Welcome/SubscribeButton';
import { useHasEntitlement } from '../../../Shared/Hook/useHasEntitlement';
import Button from '@mui/material/Button';
import { openNativeApp } from '../../../Shared/Functions/openNativeApp';
import Link from '@mui/material/Link';
import BrowserHelper from '../../../../shared/BrowserHelper';

export default function Welcome() {
    const classes = useStyles();
    const { t } = useTranslation();

    const [version, setVersion] = useState('');
    const [loading, setLoading] = useState(false);

    const { hasEntitlement, isRCIdSynced } = useHasEntitlement()
    const isMobileSafari = BrowserHelper.isMobileSafari(navigator.userAgent);

    useEffect(() => {
        async function getVersion() {
            const result = await new Request(Messages.Generic.GetVersion).sendRequest();
            const version = result.message.payload.version;
            setVersion(version);
            setLoading(false);
        }

        try {
            getVersion();
        } catch(e) {
            setVersion("");
            setLoading(false);
        }
    }, []);

    return(
        <Container component="main" maxWidth="md">
            <CssBaseline />
            {loading ?
                <div className={classes.paper}>
                    <CircularProgress />
                </div>
            :
                <div className={classes.welcomeHeader}>
                    <div className={classes.imgHeader}>
                        <HorizontalLogo width="300px" />
                    </div>
                    <Typography className={classes.testExtensionTitle} component="h2" variant="h5">
                        Welcome to Super Agent { version }
                    </Typography>
                    <br />
                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                        <Typography className={classes.testExtensionSubtitle} component="h1" variant="h6">
                            {t("update.welcome.description")}
                        </Typography>
                        <Box>
                            <Typography variant="body" sx={{
                                marginTop: "20px"
                            }}>
                                <ul>
                                    <li>Updated and improved the Extension's status detection.</li>
                                    <li>Super Agent is now available in Android for Firefox mobile. Tell your friends!</li>
                                    <li>Bug fixes.</li>
                                    {isMobileSafari && hasEntitlement && !isRCIdSynced ? (
                                        <li>Please open the app to complete the update process.</li>
                                    ) : null}
                                </ul>
                            </Typography>
                        </Box>
                        </Grid>
                    </Grid>
                    <br />
                    {!hasEntitlement && (
                        <>
                            <Typography variant="body" sx={{ marginBottom: '10px' }}>
                                Please support superagent and subscribe now!
                            </Typography>
                            <SubscribeButton />
                        </>
                    )}
                    {hasEntitlement && !isRCIdSynced && (
                        <Button
                            target="_blank"
                            component={Link}
                            href="superagent://"
                            onClick={openNativeApp}
                            variant="contained"
                            color="primary">
                                Complete update!
                        </Button>
                    )}
                    <br />
                </div>            
            }

        </Container>
    )
}
